import "core-js/modules/es.array.push.js";
import { computed, onMounted, reactive, toRefs } from 'vue';
import { useStore } from 'vuex';
import ListItem from '../../../components/ListItem.vue';
import { Toast } from 'vant';
import { useRouter } from 'vue-router';
export default {
  props: ["changeShow"],
  components: {
    ListItem
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();
    const data = reactive({
      checked: [],
      submitChecked: true
    });
    const handleChange = (value, detail) => {
      store.state.cartList.map(item => {
        if (item.id === detail.name) {
          item.num = value;
        }
      });
    };
    const init = () => {
      data.checked = store.state.cartList.map(item => item.id);
    };
    onMounted(() => {
      init();
    });
    const onSubmit = () => {
      if (data.checked.length) {
        store.commit("pay", updateData());
        router.push({
          path: "/createorder",
          query: {
            list: data.checked
          }
        });
      } else {
        Toast.fail("请选择！！！！");
      }
    };
    const choseAll = () => {
      if (data.checked.length !== store.state.cartList.length) {
        init();
      } else {
        data.checked = [];
      }
    };
    const grounpChange = result => {
      console.log(result);
      if (result.length === store.state.cartList.length) {
        data.submitChecked = true;
      } else {
        data.submitChecked = false;
      }
      data.checked = result;
    };
    const allPrice = computed(() => {
      let countList = store.state.cartList.filter(item => {
        return data.checked.includes(item.id);
      });
      let sum = 0;
      countList.forEach(item => {
        sum += item.num * item.price;
      });
      return sum * 100;
    });
    const updateData = type => {
      return store.state.cartList.filter(item => {
        return type === 'delte' ? !data.checked.includes(item.id) : data.checked.includes(item.id);
      });
    };
    const handleDelete = () => {
      // 部分删除
      // 判断data的checked是否有值
      if (data.checked.length) {
        store.commit("delete", updateData("delete"));
        data.checked = [];
        // 购物车没有数据的时候
        if (!store.state.cartList.length) {
          props.changeShow();
          store.commit('edit', 'delete');
        }
      } else {
        Toast.fail("请选择要删除的商品");
      }
      // 全部删除
    };

    return {
      ...toRefs(data),
      store,
      handleChange,
      onSubmit,
      choseAll,
      grounpChange,
      allPrice,
      handleDelete
    };
  }
};